import { ip } from '../networkPrefix'
import { get,post,patch } from 'axios'
import axios from 'axios'


export default {

    novoContratoEmpresa(item){
        
        return new Promise(async (resolve,reject)=>{
            try {


            
                const data = (await post(`${ip}/empresa`,{},{params:item})).data

                resolve(data)
                
            } catch (error) {

                reject(error)

                throw error
            }


        })    

    },

    getTodoEmpresa(){
        
        return new Promise(async (resolve,reject)=>{
            try {

            
                const data = (await get(`${ip}/empresa`)).data
        

                resolve(data)

            } catch (error) {

                reject(error)
                throw error
            }


        })    

    },
    
    updateEmpresaById(item){
        
        
        return new Promise(async (resolve,reject)=>{
            try {

                const data = (await axios.patch(`${ip}/empresa`,{},{params:item})).data
                resolve(data)
                
            } catch (error) {

                reject(error)

                throw error
            }


        })    
    },
    
    apagarEmpresaById(item){
        
        
        return new Promise(async (resolve,reject)=>{
            try {


                const data = (await axios.delete(`${ip}/empresa/apagar`,{ params: item })).data
                resolve(data)
  
                
            } catch (error) {

                reject(error)

                throw error
            }


        })    
    }




}