import { ip } from '../networkPrefix'
import axios, { get,post } from 'axios'


export default {


    getExtratos(id_contrato){
        
        
        return new Promise(async (resolve,reject)=>{
            try {
                
                const data = (await axios.get(`${ip}/corrente`,{params:{id_contrato:id_contrato}})).data

                resolve(data)

            } catch (error) {

                reject(error)

                throw error
            }


        })    

    },
    async apagarExtrato(i){
        
        return new Promise(async (resolve,reject)=>{
            try {
                
                const data = (await axios.delete(`${ip}/corrente`,{params: i })).data

                resolve(data)

            } catch (error) {

                reject(error)

                throw error
            }


        })    

    

    },
  
    postNovoExtrato(f){
    
        
        
        return new Promise(async (resolve,reject)=>{
            try {

                
                const data = (await post(`${ip}/corrente`,{},{params:f})).data
                

                resolve(data)

            } catch (error) {

                reject(error)
                throw error
            }


        })    

    
    }




}