import toastify from 'toastify-js'
import vuetify from "../../plugins/vuetify";





export default {

    primary(text,duration){

        toastify({
            text:text,
            duration:duration,
            close:true,
            gravity:'bottom',
            position:'left',
            backgroundColor:"#004D40",
          
          }).showToast()
      



    },


    err(text,duration){

        toastify({
            text:text,
            duration:duration,
            close:true,
            gravity:'bottom',
            position:'left',
            backgroundColor:'#E53935',
          
          }).showToast()
      



    }




} 