import Vue from "vue";
import VueRouter from "vue-router";
import Home from "../views/Home.vue";
import stock from "../views/stock.vue";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "Menu Principal",
    component: Home,
  },
  {
    path: "/entradaNegocio",
    name: "Entrada",
    component: ()=>import("@/views/negocio/entrada.vue"),
  },
  {
    path: "/novoNegocio",
    name: "Novo Negócio",
    component: ()=>import("@/views/negocio/novo.vue"),
  },
  {
    path: "/estoque",
    name: "stock",
    component: stock,
  },

];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes,
});

export default router;
