import { ip } from '../networkPrefix'
import { get,post,patch } from 'axios'
import axios from 'axios'




export default {

    novoContratoByIdEmpresa(item){
        
        return new Promise(async (resolve,reject)=>{
            try {
            
                const data = (await axios.post(`${ip}/empresa/contrato`,{},{params: item })).data
                resolve(data)
                
            } catch (error) {

                reject(error)

                throw error
            }


        })    

    },
   async getContratosById(item) {

    return new Promise(async (resolve,reject) => {
  
        try {

            const data = (await get(`${ip}/empresa/contrato`,{ params:{id_empresa:item} })).data

            resolve(data)
            
        } catch (error) {
            
            reject(error)
            throw error
            
        }
    })



    },
    apagarContrato(item){
        

    return new Promise(async (resolve,reject) => {
  
        try {

            const data = (await axios.delete(`${ip}/contrato`,{ params:item.item })).data

            resolve(data)
            
        } catch (error) {
            
            reject(error)
            throw error
            
        }
    })

    },



}